import React from "react";
import { formatMsg } from "@/locales";
import styles from "./index.scss";

const Modal: any = props => {
    const { list, setState } = props;
    const { showModal: show } = list;
    const type = list.modalType();
    const listType = list.listType();
    const hintType = listType === "pic" ? "pic_modal_del_hint" : listType === "audio" ? "audio_modal_del_hint" : "";
    const noStorageHintType = listType === "pic" ? "entry_no_storage_pic" : listType === "audio" ? "entry_no_storage_audio" : "";
    const modals = {
        // 新需求no_storage被删除
        no_storage: [formatMsg(noStorageHintType), formatMsg("entry_no_storage_cancel"), formatMsg("entry_no_storage_open")],
        del_from_gallery: [formatMsg(hintType), formatMsg("pic_modal_del_cancel"), formatMsg("pic_modal_del_confirm")],
        no_vip:[formatMsg("no_vip_modal"), formatMsg("no_vip_modal_left"), formatMsg("no_vip_modal_right")],
        vip_overdue:[formatMsg("vip_overdue_modal"), formatMsg("vip_overdue_modal_left"), formatMsg("vip_overdue_modal_right")],
        vip_upgrade:[formatMsg(`vip_upgrade_${listType}_modal`), formatMsg("vip_upgrade_modal_left"), formatMsg("vip_upgrade_modal_right")],
        space_full:[formatMsg(`space_${listType}_full`), formatMsg("space_full_sure")]
    };

    const modalMsg = modals[type];

    const rightClick = e => {
        if (type === "del_from_gallery") {
            // 通知列表页删除事件
            setState({ deleteEvent: true, showModal: false });
        }
        if (type === "no_vip" || type === "vip_overdue" || type === "vip_upgrade"){
            // 移动端无VIP开通
            setState({ showMsg: true, msgIndex: 11 })
        }
    };
    const leftClick = e => {
        // 取消删除
        setState({ showModal: false });
        // 借用下层提示框后返回上级
        if(type === "no_vip" || type === "vip_overdue" ){
            window.parent &&  window.parent.postMessage("HideLibrary", "*");
        }
        if(type === "vip_upgrade" || type === "space_full"){
            // 点击删除
            list.vipInfo.modalIndex = 0;
        }
    };
    return (
        <div className={styles["modal"]} style={{ display: show ? "block" : "none" }}>
            <div className={styles["msgBox"]}>
                <div className={styles["hint"]}>
                    <div className={styles["hintLine"]}>{modalMsg[0]}</div>
                </div>
                <div className={styles["chose"]}>
                    <div className={styles["left"]} onTouchStart={leftClick} style={!modalMsg[2] ? {width:'100%',color:"rgb(0, 122, 255)"} : {}}>
                        {modalMsg[1]}
                    </div>
                    {modalMsg[2] && <div className={styles["right"]} onTouchStart={rightClick}>
                        {modalMsg[2]}
                    </div>}
                </div>
            </div>
            <div
                className={styles["curtain"]}
                onTouchStart={e => {
                    // e.preventDefault();
                    e.stopPropagation();
                }}
            ></div>
        </div>
    );
};

export default Modal;
