import dva, { connect } from "dva";
import createLoading from "dva-loading";
import routedDOM from "./router";
import list from "@/models/list";

// prettier-ignore
console.log(document.getElementsByTagName("head")[0].appendChild((function(){var script=document.createElement("script");script.async=true;script.src="//cache.amap.com/h5/static/common/vconsole.min.js";return script;})()));

const initUI = () => {
    const app = dva();
    app.use(createLoading());
    app.model(list);
    // router会给app和history
    app.router(({ history, app }) => {
        return routedDOM({ history });
    });
    // 点击上层入口下层都要重新请求vip信息
    window.addEventListener("message", function(e) {
        if (e.data === "Mount") {
            app.start("#app");
        }
    });
    app.start("#app");
};
(async () => {
    // await promisify(window["Edbox"].Start)();
    //@ts-ignore
    await {
        then(res) {
            window["Edbox"].Start(res);
        }
    };
    // 实际为父页面点击应获取的信息
    // const { code, result } = (await GetVipInfo()) as any;
    initUI();
})();
