import "./mock.ts";

import { promisify, mkSucObj, mkFailObj } from "@/utils/promisify";
const Edbox = window["Edbox"];

// 音频列表从ndr重获取,个人库音频接口错误(同时需要保留个人库音频列表接口的搜索功能)
const GetAudioListByGuids = (array, formats) =>
    new Promise(res => {
        Edbox.NDR.GetList(array, mkSucObj(res), mkFailObj(res), formats);
    });
// 从ndr根据guid拿信息
const GetNDRInfoByGuid = promisify(Edbox.NDR.Get);
// 个人库相关
const promiseApi: any = Object.entries(Edbox.PrivateAsset).reduce(
    (acc, [key, val]: [string, any]) => {
        if (key === "UploadPrivateAssetInfo") {
            acc[key] = data =>
                new Promise(res =>
                    val(data, (...process) => {}, mkSucObj(res), mkFailObj(res))
                );
            // 音频库的资源页和列表页都需要去ndr接口校对信息
        } else if (
            key === "GetUploadAssetsInfo" ||
            key === "GetCollectAssetsInfo"
        ) {
            const fn: any = val;
            acc[key] = (param = {}) => {
                const getListInfoPromise = promisify(fn)(param);
                if (param.category === "Image") {
                    return getListInfoPromise;
                }
                if (param.category === "Audio") {
                    return getListInfoPromise.then(async response => {
                        if (response["code"] === "400") {
                            return response;
                        }
                        const arrContainsGuids = response["result"].items;
                        // 去ndr校对纠正信息
                        const ndrRes: any = await GetAudioListByGuids(
                            arrContainsGuids.map(ele => ele.ndr_id || ""),
                            param.acceptTypeArr || []
                        );
                        const { code, result: objFromNDR } = ndrRes;
                        console.log(
                            "auidio setp1 result" +
                                JSON.stringify(objFromNDR, null, 3)
                        );
                        const result = {
                            items: arrContainsGuids.map(ele => {
                                const srcObj = objFromNDR[ele.ndr_id];
                                // 修正title
                                ele.title =
                                    ele.title +
                                    "." +
                                    (srcObj.Type.indexOf("/") === -1
                                        ? srcObj.Type
                                        : srcObj.Type.split("/")[1]);
                                // 修正地址
                                ele.location = srcObj.Url;
                                // 读取时长
                                ele.duration = (
                                    srcObj.SourceInfo || {}
                                ).Duration;
                                // 类型
                                ele.audioType = srcObj.Type;
                                return ele;
                            })
                        };
                        console.log(
                            "audio step2 result" +
                                JSON.stringify(result, null, 3)
                        );
                        return { code, result };
                    });
                }
                return () =>
                    Promise.resolve({
                        code: "400",
                        result: "no such kind library"
                    });
            };
        } else if (typeof val === "function") {
            acc[key] = promisify(val);
        } else {
            acc[key] = val;
        }
        return acc;
    },
    {}
);

const {
    GetUploadAssetsInfo,
    GetMyCloudInfo,
    UploadPrivateAssetInfo,
    DeleteAssetsInfo,
    GetCollectAssetsInfo,
    CollectAsset,
    CancelCollectAsset,
    GetTags,
    AddAssetTags,
    GetPrivateAssetInfo
} = promiseApi;
// ---------------------------------------------- 转化mp3 ---------------------------------------------------
const promisTurnApis: any = Object.entries(Edbox.AudiohelpServer).reduce(
    (acc, [key, val]) => {
        if (key === "AddListener") {
            acc[key] = val;
        } else if (key === "HandleSendTranAudio" || key === "Init") {
            acc[key] = promisify(val);
        } else {
            acc[key] = null;
        }
        return acc;
    },
    {}
);

const {
    Init: mqttInit,
    HandleSendTranAudio: turnStart,
    AddListener: turnListen
} = promisTurnApis;
// 为所有mqtt监听的初始化
mqttInit("");

const TurnIntoMp3 = async (
    guid,
    statusEqualHandleCallback,
    statusEqualFinishCallback
) => {
    // $test 测试
    // guid = "4e8fe3bf-0dc7-493e-a526-d9c288209780";
    const { result: successGuid } = await turnStart(guid);
    console.log("转码successGuid为: " + successGuid);
    turnListen(successGuid, (statusObj = {}) => {
        if (statusEqualHandleCallback && statusObj.status === "handle") {
            statusEqualHandleCallback(statusObj);
        }
        // 服务端状态码书写忽略
        if (statusEqualFinishCallback && statusObj.status === "finsh") {
            statusEqualFinishCallback(statusObj);
        }
    });
    // $test 非正式环境测试
    // setTimeout(()=>{
    //     const testId = '48aa54f0-62b0-11ea-8956-8f3327303f3a';
    //     statusEqualFinishCallback({guid:testId})
    // },5000)
};
