import React, { Component } from "react";
import { connect } from "dva";
import { withRouter } from "dva/router";
import { MusicIcon, ImgIcon, RightArrow } from "@/components/FontIcon";
import { formatMsg } from "@/locales";
import styles from "./index.scss";
import StatusLoading from "../Loading";
@connect(({ loading: { effects } }) => ({
    loading: effects["list/getCloudInfo"]
}))
class FontPage extends Component {
    props: any;
    calcGB(num: number = 1) {
        const storage = Number((num / 1073741824).toFixed(2));
        const arr = storage.toString().split(".");
        return arr[0] + "." + ((arr[1] || "") + "00").slice(0, 2);
    }
    calcPercent(used: string = "1", total: string = "1") {
        return Number((+used / +total) * 100).toFixed(2) + "%";
    }
    // 空间不够
    async spaceNotEnough(type) {
        // 校验空间是否足够
        const { list, setState, dispatch } = this.props;
        const listType = list.listType();
        // 可能vip信息变动，再去请求一次,再去请求容量信息
        const [
            data,
            {
                result: { total = 1, used = 0 }
            }
        ] = await Promise.all([
            dispatch({ type: "list/checkVip", render: false }),
            dispatch({
                type: "list/getWhichTypeCloudInfo",
                payload: listType === "pic" ? "Image" : "Audio"
            })
        ]);
        list.vipInfo.tabState(data);
        const { goldVip, carbonVip } = list.vipInfo;
        if (total <= used) {
            if (goldVip) {
                // 黄金提示升级
                setState({ showModal: true, modalIndex: 4 });
                return false;
            }
            if (carbonVip) {
                // 钻石提示空间不足
                setState({ showModal: true, modalIndex: 5 });
                return false;
            }
            return false;
        }
        return true;
    }
    // 点图片管理
    async enterPicBarn() {
        const { router, setState } = this.props;
        setState({ typeChosed: 0 }, false);
        if (!(await this.spaceNotEnough("pic"))) return;
        router.tabMode("pic");
    }
    // 点音频管理
    async enterAudioBarn() {
        const { router, setState } = this.props;
        setState({ typeChosed: 1, navId: "" }, false);
        if (!(await this.spaceNotEnough("audio"))) return;
        router.tabMode("audio");
    }
    componentDidMount() {
        const { dispatch } = this.props;
        // 磁盘空间,payload参数先后顺序不可变
        dispatch({
            type: "list/getCloudInfo",
            payload: [{ category: "Image" }, { category: "Audio" }]
        });
    }

    render() {
        const { router, list, setState, loading } = this.props;
        const { pic_total, pic_used, audio_total, audio_used } = list;
        const picTotal = this.calcGB(pic_total);
        const picUsed = this.calcGB(pic_used);
        const audioTotal = this.calcGB(audio_total);
        const audioUsed = this.calcGB(audio_used);
        const picPercent = this.calcPercent(picUsed, picTotal);
        const audioPercent = this.calcPercent(audioUsed, audioTotal);
        return (
            <div className={`${styles["libsList"]}`}>
                <div className={`${styles["listBox"]}`}>
                    {loading ? (
                        <StatusLoading
                            pageTab
                            backgroundColor={"transparent"}
                        />
                    ) : (
                        <div className={`${styles["list"]}`}>
                            {/* 图库 */}
                            <div
                                className={`${styles["listItem"]}`}
                                onTouchStart={this.enterPicBarn.bind(this)}
                            >
                                <div className={`${styles["left"]}`}>
                                    <div className={`${styles["pic"]}`}>
                                        <img src={ImgIcon} alt="" />
                                    </div>
                                </div>
                                <div className={`${styles["middle"]}`}>
                                    <div className={`${styles["txt"]}`}>
                                        <div className={`${styles["t1"]}`}>
                                            {formatMsg("entry_pic_title")}
                                        </div>
                                        <div className={`${styles["t2"]}`}>
                                            {formatMsg("entry_used")} {picUsed}{" "}
                                            GB / {picTotal} GB
                                        </div>
                                    </div>
                                    <div className={`${styles["bar"]}`}>
                                        <div
                                            className={`${styles["live"]}`}
                                            style={{ width: picPercent }}
                                        ></div>
                                    </div>
                                </div>
                                <div className={`${styles["right"]}`}>
                                    <img src={RightArrow} alt="" />
                                </div>
                            </div>
                            {/* 音频库 */}
                            <div
                                className={`${styles["listItem"]}`}
                                onTouchStart={this.enterAudioBarn.bind(this)}
                            >
                                <div className={`${styles["left"]}`}>
                                    <div className={`${styles["pic"]}`}>
                                        <img src={MusicIcon} alt="" />
                                    </div>
                                </div>
                                <div className={`${styles["middle"]}`}>
                                    <div className={`${styles["txt"]}`}>
                                        <div className={`${styles["t1"]}`}>
                                            {formatMsg("entry_audio_title")}
                                        </div>
                                        <div className={`${styles["t2"]}`}>
                                            {formatMsg("entry_used")}{" "}
                                            {audioUsed} GB / {audioTotal} GB
                                        </div>
                                    </div>
                                    <div className={`${styles["bar"]}`}>
                                        <div
                                            className={`${styles["live"]}`}
                                            style={{ width: audioPercent }}
                                        ></div>
                                    </div>
                                </div>
                                <div className={`${styles["right"]}`}>
                                    <img src={RightArrow} alt="" />
                                </div>
                            </div>
                        </div>
                    )}
                </div>
            </div>
        );
    }
}

export default FontPage;
