import React, { Component, Fragment } from "react";
import Header from "@/components/Header";
import Modal from "@/components/Modal";
import Operate from "@/components/Operate";
import AudioPlayer from "@/components/AudioPlayer";
import Msg from "@/components/Msg";
import { connect } from "dva";

import styles from "./index.scss";
@connect(({ list }) => ({ list }))
class Main extends Component {
    props: any;
    // state = {
    //     picCacheIds: [],
    //     audioCacheIds: []
    // };
    async componentDidMount() {
        const {
            // vipInfo: { isVip, vipOverdue },
            dispatch
        } = this.props;
        await dispatch({
            type: "list/checkVip"
        });
        if (this.props.list.vipInfo.isVip) {
            dispatch({
                type: "list/listenVipExpire",
                expireAlert: this.ifVipExpire.bind(this)
            });
        }
        this.globalWatcher = () => {
            this.updateState({ showPlayer: false, stopAudio: true }, false);
        };
        window.addEventListener("popstate", this.globalWatcher);
    }
    componentWillUnmount() {
        window.removeEventListener("popstate", this.globalWatcher);
    }
    globalWatcher() {}
    updateState(newState: object, refresh = true) {
        const { list } = this.props;
        Object.assign(list, newState);
        refresh && this.setState({});
    }
    ifVipExpire() {
        this.props.list.vipInfo.tabState("EXPIRED");
        this.checkVip(true);
    }
    inCasePageReload() {
        const { router } = this.props;
        // 当前页刷新,默认图片库（收藏页刷新暂不做刷新保存)
        router.isMode("choose") &&
            this.updateState({ showOperate: true }, false);
        router.isMode("audio") && this.updateState({ typeChosed: 1 }, false);
    }
    checkVip(ifrender = false) {
        const {
            router,
            list: {
                vipInfo: { isVip, vipOverdue, modalIndex }
            }
        } = this.props;
        console.log(isVip, modalIndex);

        if (modalIndex) {
            this.updateState({ showModal: true, modalIndex }, ifrender);
        }
        // router.isMode("entry");
    }
    // 拿取更多
    async getMore(refresh = true) {
        console.log(`%c 'getmore `, "color:red");
        const { dispatch, list } = this.props;
        // 重置是否正在搜索
        list.searching && (list.searching = false);

        // 重置全选
        list.chooseAll = false;
        const listStorage = list.listStorage();
        if (listStorage.isAll) {
            return;
        }
        const { navId, keyword } = list;
        // 清空上传成功项和资源重复项
        list.splicePreviewArr();
        // 是否去拿标签
        const tagStorage = list.tagStorage();
        if (tagStorage.refetch) {
            await this.getTags();
        }
        const listType = list.listType();
        const headInfo = list.headInfo();
        const { id } = headInfo;
        const { page, size } = listStorage;
        const category = this.chooseParam("category");

        const payload =
            id === "list"
                ? {
                      category,
                      tags: navId,
                      word: keyword,
                      page,
                      size
                  }
                : {
                      category,
                      tech_key: "source",
                      word: keyword,
                      page,
                      size
                  };
        const datas = await dispatch({
            type: "list/getListAll",
            payload,
            lib: listType,
            mode: id,
            refresh
        });
    }
    chooseParam(prop) {
        const checkParam = {
            category: {
                pic: "Image",
                audio: "Audio"
            }
        };
        switch (prop) {
            case "category":
                return checkParam[prop][this.props.list.listType()] || "";

            default:
                return "";
        }
    }

    // 拿tags列表
    async getTags() {
        const { dispatch, list } = this.props;
        // listType // pic 或 audio
        const tagStorage = list.tagStorage();
        // 标签无总数，拿一次就行
        tagStorage.refetch = false;
        const tagArrResult = await dispatch({
            type: "list/getTags",
            payload: { category: this.chooseParam("category") }
        });
        tagStorage.items = tagArrResult;
        return;
    }
    render() {
        console.log("main render");
        const {
            location: { pathname },
            getChildByRoute,
            list: {
                vipInfo: { isVip, vipOverdue }
            }
        } = this.props;
        this.inCasePageReload();
        this.checkVip();
        const Target = getChildByRoute(pathname);
        return (
            <Fragment>
                {isVip && !vipOverdue ? (
                    <div className={styles["basic"]}>
                        <Header
                            {...this.props}
                            setState={this.updateState.bind(this)}
                            getMore={this.getMore.bind(this)}
                        />
                        <Target
                            {...this.props}
                            setState={this.updateState.bind(this)}
                            getMore={this.getMore.bind(this)}
                        />
                        <Operate
                            {...this.props}
                            getMore={this.getMore.bind(this)}
                            setState={this.updateState.bind(this)}
                        />
                        <AudioPlayer
                            {...this.props}
                            setState={this.updateState.bind(this)}
                        />
                    </div>
                ) : null}
                <Msg {...this.props} setState={this.updateState.bind(this)} />
                <Modal {...this.props} setState={this.updateState.bind(this)} />
            </Fragment>
        );
    }
}
export default Main;
